import React, { useState, useRef, useEffect } from "react";
import {
  View,
  Text,
  Dimensions,
  TextInput,
  TouchableOpacity,
  ScrollView,
  KeyboardAvoidingView,
  Keyboard,
  KeyboardEvent,
} from "react-native";
import useKeyboardHeight from "react-native-use-keyboard-height";
import Add_audio_svg from "../../../assets/svg/Add_audio_svg";
import Add_photo_svg from "../../../assets/svg/Add_photo_svg";
import Back_button from "../../../assets/svg/Back_button";

const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

export default function CreateExpress({ route, navigation }) {
  const { item, deleteListItemByPK } = route.params; // the express item
  //const [keyboardHeight, setKeyboardHeight] = useState(0); // not in use in web platform
  //const keyboardHeight = useKeyboardHeight();
  const [textValue, setTextValue] = useState(""); // text input value
  const textInputRef = useRef();
  const onWander = () => {
    if (textValue.length !== 0) {
      console.log("===onWander, textValue:", textValue);
      navigation.navigate("ExpressDetail", {
        item,
        textValue,
        deleteListItemByPK,
        ifCreateExpression: true,
      });
    }
  };
  const onBack = () => {
    navigation.navigate("Wander");
  };
  // keyboardTrackingView now is not tracking, just for reusing from dialog, contain the three button at the bottom
  const renderKeyboardTrackingView = () => {
    return (
      <View
        style={{
          width: "100%",
          height: 62,
          flexDirection: "column",
        }}
      >
        <Text
          style={{
            alignSelf: "flex-end",
            marginRight: 20,
            fontSize: 15,
          }}
        >
          {textValue.length}/1000
          {/* isKeyboardShow{isKeyboardShow.toString()} Platform.OS === "web"{" "}
          {(Platform.OS === "web").toString()} */}
        </Text>
        <View
          style={{
            width: "100%",
            flex: 1,
            flexDirection: "row",
            alignSelf: "center",
          }}
        >
          <TouchableOpacity style={{ marginLeft: 20, alignSelf: "center" }}>
            <Add_photo_svg style={{ width: 35, height: 35 }} />
          </TouchableOpacity>
          <TouchableOpacity style={{ marginLeft: 3, alignSelf: "center" }}>
            <Add_audio_svg style={{ width: 35, height: 35 }} />
          </TouchableOpacity>
          <View style={{ flex: 1 }} />
          <TouchableOpacity
            style={{
              height: 32,
              width: 80,
              backgroundColor: textValue.length == 0 ? "gainsboro" : "black",
              alignSelf: "center",
              marginRight: 20,
              borderRadius: 5,
              alignItems: "center",
              justifyContent: "center",
            }}
            onPress={onWander}
            enabled={textValue.length != 0}
          >
            <Text style={{ color: "white", fontSize: 15, fontWeight: 600 }}>Post</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  };
  return (
    <View
      style={{
        height: windowHeight,
        width: windowWidth,
        backgroundColor: "white",
        alignSelf: "center",
        //borderTopRightRadius: 15,
        //borderTopLeftRadius: 15,
        flexDirection: "column",
      }}
    >
      <View
        style={{
          marginTop: 20,
          marginBottom: 10,
          marginHorizontal: 12,
          //backgroundColor: "pink",
          flexDirection: "row",
        }}
      >
        {/* back button */}
        <TouchableOpacity onPress={onBack}>
          <Back_button
            style={{
              width: 24,
              height: 24,
              marginTop: 2,
            }}
          />
        </TouchableOpacity>
        {/* question topic */}
        <Text
          style={{
            fontSize: 21,
            marginLeft: 10,
            fontWeight: 700,
            fontFamily: "Merriweather-Regular",
          }}
        >
          {item.express_question}
        </Text>
      </View>
      <ScrollView
        //keyboardShouldPersistTaps="handled"
        style={{
          flexDirection: "row",
        }}
      >
        {/* the indicator, when no text is in input, it will appear */}
        <View style={{ width: "100%" }}>
          <Text
            style={{
              fontSize: 14,
              marginTop: 8,
              marginHorizontal: 20,
              fontWeight: 700,
              color: "gray",
            }}
          >
            Express, whatever comes to your mind,
          </Text>
          <Text
            style={{
              fontSize: 14,
              marginTop: 5,
              marginBottom: 20,
              marginHorizontal: 20,
              fontWeight: 300,
              color: "gray",
            }}
          >
            and connect with like-minded wanderers!
          </Text>
        </View>

        <TextInput
          // autoFocus={true}
          multiline
          maxLength={1000}
          rows={10}
          onChangeText={text => setTextValue(text)}
          value={textValue}
          style={{
            width: windowWidth - 40,
            height: 150,
            fontSize: 19,
            marginVertical: 12,
            marginHorizontal: 20,
            color: "black",
            alignSelf: "flex-start",
            fontWeight: 500,
          }}
          ref={textInputRef}
          placeholder="Type here to answer..."
          placeholderTextColor={"#676767"}
        />
        {renderKeyboardTrackingView()}
      </ScrollView>
    </View>
  );
}
