import { View, Text, Image, TouchableOpacity } from "react-native";
import React from "react";

// the message chat component
export default function UserMessage({
  name,
  icon,
  text,
  ifLeftAvatar,
  ifBlue,
  userID,
  navigation,
}) {
  const onOpenUserDetail = () => {
    if (userID) {
      navigation.push("UserExpressList", { userID, userIcon: icon, userName: name });
    }
  };
  // ifLeftAvatar can determine if its the user itself message or others message
  return (
    <View
      style={{
        width: "100%",
        marginBottom: 15,
        flexDirection: ifLeftAvatar ? "row" : "row-reverse",
      }}
    >
      {/* user icon row */}
      <TouchableOpacity onPress={onOpenUserDetail}>
        <View
          name="icon"
          style={{
            width: 36,
            height: 36,
            borderRadius: 36,
            marginHorizontal: 10,
            overflow: "hidden",
          }}
        >
          <Image
            style={{ width: "100%", height: "100%", borderRadius: 36 }}
            source={{ uri: icon }}
          />
        </View>
      </TouchableOpacity>
      {/* message row */}
      <View
        name="middle"
        style={{
          flex: 1,
          flexDirection: "column",
        }}
      >
        {/* text white area */}
        <Text
          style={{
            color: "white",
            fontSize: 14,
            alignSelf: ifLeftAvatar ? "flex-start" : "flex-end",
            fontWeight: 500,
            paddingVertical: 2,
          }}
        >
          {name}
        </Text>

        <View
          name="message"
          style={{
            flex: 1,
            backgroundColor: "rgba(255,255,255,0.8)",
            marginTop: 3,
            paddingHorizontal: 12,
            paddingVertical: 12,
            borderRadius: 10,
          }}
        >
          <Text
            style={{
              fontSize: 15,
              fontWeight: 400,
              color: ifBlue ? "#4858B6" : "black",
            }}
          >
            {text}
          </Text>
        </View>
      </View>
      {/* row for same space of avatar */}
      <View
        name="dummyView"
        style={{
          width: 36,
          height: 36,
          marginHorizontal: 10,
        }}
      ></View>
    </View>
  );
}
