import { View, Text, TouchableOpacity, Image, ActivityIndicator, ScrollView } from "react-native";
import React, { useState, useEffect } from "react";
import { getUserExpressQuestionList } from "@/api";
import Back_button from "../../../assets/svg/Back_button";

export default function UserExpressList({ route, navigation }) {
  //for static test
  //let userID = "user#user_2YaweNj36RjSiEBpNpgbJ9BszQc";
  //const userIcon =
  "https://img.clerk.com/eyJ0eXBlIjoicHJveHkiLCJzcmMiOiJodHRwczovL2ltYWdlcy5jbGVyay5kZXYvb2F1dGhfZ29vZ2xlL2ltZ18yWUZJWVNLSnJzU1VhR1JuMXNNNEtVNG5CQmEifQ";
  //const userName = "piaomz";

  let { userID, userIcon, userName } = route.params;
  //for dev
  if (__DEV__) {
    userID = "user#user_2YaweNj36RjSiEBpNpgbJ9BszQc";
  }
  console.log("===UserExpressList:", userID, userIcon, userName);
  const [isLoading, setIsLoading] = useState(false);
  const [expressList, setExpressList] = useState([]);
  const onPressExpression = express_item => {
    navigation.push("ExpressDetail", {
      ifCreateExpression: false,
      item: express_item,
      user_id: userID,
    });
  };
  useEffect(() => {
    setIsLoading(true);
    getUserExpressQuestionList({
      user: userID,
    }).then(
      res => {
        setExpressList(res);
        setIsLoading(false);
        console.log("====getUserExpressQuestionList", res);
      },
      error => {
        console.log("====getUserExpressQuestionList Error", error);
        setIsLoading(false);
      }
    );
  }, []);
  const onBack = () => {
    navigation.goBack();
  };
  return (
    <View
      style={{
        width: "100%",
        height: "100%",
        backgroundColor: "#878787",
        flexDirection: "column",
      }}
    >
      <View
        style={{
          marginTop: 20,
          marginBottom: 20,
          marginHorizontal: 12,
          //backgroundColor: "pink",
          flexDirection: "row",
        }}
      >
        {/* back button */}
        <TouchableOpacity onPress={onBack}>
          <Back_button
            style={{
              width: 24,
              height: 24,
              marginTop: 2,
              color: "white",
            }}
          />
        </TouchableOpacity>
      </View>
      <View name="avatarAndName" style={{ flexDirection: "row", marginHorizontal: 40 }}>
        <View
          name="icon"
          style={{
            width: 64,
            height: 64,
            borderRadius: 12,
            overflow: "hidden",
          }}
        >
          <Image style={{ width: "100%", height: "100%" }} source={{ uri: userIcon }} />
        </View>
        <View
          name="userName"
          style={{
            flex: 1,
            marginLeft: 16,
            flexDirection: "column",
            justifyContent: "center",
          }}
        >
          <Text
            style={{
              color: "white",
              fontSize: 17,
              fontWeight: 700,
              marginBottom: 5,
            }}
          >
            {userName}
          </Text>
          <Text
            style={{
              color: "white",
              fontSize: 15,
            }}
          >
            User ID @{userName}
          </Text>
        </View>
      </View>
      <Text
        style={{
          color: "white",
          fontSize: 18,
          fontWeight: 700,
          marginBottom: 10,
          marginHorizontal: 15,
          marginTop: 40,
        }}
      >
        His/Her Wanders....
      </Text>
      <ScrollView
        style={{
          flex: 1,
          paddingHorizontal: 15,
        }}
      >
        {isLoading ? (
          <ActivityIndicator size="large" color="#FFFFFF" />
        ) : (
          expressList.map((item, i) => (
            <RenderExpress key={i} item={item} onPressExpression={onPressExpression} />
          ))
        )}
      </ScrollView>
    </View>
  );
}

function RenderExpress({ item, onPressExpression }) {
  return (
    <TouchableOpacity
      style={{
        backgroundColor: "rgba(255,255,255,0.15)",
        borderRadius: 12,
        marginVertical: 6,
        paddingHorizontal: 15,
        paddingVertical: 18,
      }}
      onPress={() => {
        onPressExpression(item);
      }}
    >
      <Text
        style={{
          color: "white",
          fontSize: 17,
          fontWeight: 600,
        }}
      >
        {item.express_question}
      </Text>
    </TouchableOpacity>
  );
}
